<template>
  <b-container>
  <h1>List of Persons</h1>
    <b-table striped hover :items="persons" :fields="fields">
      <template #cell(actions)="row">
        <b-button @click="editItem(row.item.idperson)">Edit</b-button>
        <b-button @click="deleteItem(row.item.idperson)" variant="danger">Delete</b-button>
      </template>

    </b-table>
  </b-container>
</template>

<script>
import axios from 'axios';
export default {
name: "Persons",
  data(){
    return{
      persons:[],
      errors:[],
      fields:[
        {key:'idperson', label:'Person ID'},
        {key:'nickname'},
        {key:'firstname'},
        {key:'lastname'},
        {key:'birthday'},
        {key:'height'},
        {key:'gender'},
        {key:'actions'}
      ]
    }
  },
  created(){
    axios.get("/persons")
    .then(response =>{

      this.persons = response.data
    })
    .catch(e => {
      this.errors.push(e)
        })
  },
  methods:{
    deleteItem(id){
      axios.delete("/persons/"+id)
      .then(response =>{
        console.log(response);
        const index = this.persons.findIndex(person=>person.idperson == id);
        if(~index){
          this.persons.splice(index, 1);
        }
      })
    },
    editItem(id){
      this.$router.push({path:"/editperson/"+id});
    }
  }
}
</script>

